<script>
  function AllData (that) { //数据类
    this.data = that.$store.getters.getFormContent('invoice_form') // 数据
    this.getVioceZhengfei = () => { // 获取所有废票和 非废票 的所有的方法
      let feipiao = []
      let piao = []
      for (let i in this.data) {
        if (this.data[i].isZuofei) {
          feipiao.push(this.data[i])
        } else {
          piao.push(this.data[i])
        }
      }
      let all = {}
      all.feipiao = feipiao
      all.piao = piao
      return all
    }
    this.zhengVioce = this.getVioceZhengfei().piao //正票的所有
    this.feiVioce = this.getVioceZhengfei().feipiao //废票的所有
    this.getGoodsZhengfu = (goodsArr) => { // 获取商品数组的正负的方法，返回正负俩obj
      let data = {}
      let zheng = []
      let fu = []
      for (let i in goodsArr) {
        if (goodsArr[i].count > 0) {
          zheng.push(goodsArr[i])
        }
        if (goodsArr[i].count < 0) {
          fu.push(goodsArr[i])
        }
      }
      data.zheng = zheng
      data.fu = fu
      return data
    },
      this.numJine = (arr) => { // 批量计算金额的方法
        let num = 0
        for (let i in arr) {
          num += arr[i].count * arr[i].CCP_UnitPrice
        }
        return num.toFixed(2)
      },
      this.getPercentArr = (arr) => { // 给数组里面按税率分类
        let endData = []
        let arrData = []
        for (let j = 0; j < 8; j++) {
          arrData[j] = []
        }
        for (let i in arr) {
          switch (arr[i].CCP_taxrate) {
            case '16%':
              arrData[0].push(arr[i])
              break
            case '13%':
              arrData[1].push(arr[i])
              break
            case '10%':
              arrData[2].push(arr[i])
              break
            case '9%':
              arrData[3].push(arr[i])
              break
            case '6%':
              arrData[4].push(arr[i])
              break
            case '4%':
              arrData[5].push(arr[i])
              break
            case '3%':
              arrData[6].push(arr[i])
              break
            default:
              arrData[7].push(arr[i])
              break
          }
        }
        for (let i in arrData) {
          endData[i] = this.numJine(arrData[i])
        }
        return endData
      }
  }

  function ZhengfeiJine () {
    //   AllData.call(this, that)
    this.getData = () => { // 获取正废正负等数据的方法
      let feipiao = this.feiVioce
      let data = []
      for (let i in feipiao) {
        if (this.getGoodsZhengfu(feipiao[i].piao_form.invoice_table_data).zheng[0]) {
          data.push(this.getGoodsZhengfu(feipiao[i].piao_form.invoice_table_data).zheng[0])
        }
      }
      return data
    }
    this.data = this.getData()
    this.num = this.numJine(this.data)
    this.name = '销项正废金额'
    this.percentArr = this.getPercentArr(this.data)
  }

  function ZhengshuJine () {
    //   AllData.call(this, that)
    this.getData = () => { // 获取正废正负等数据的方法
      let piao = this.data
      let data = []
      for (let i in piao) {
        if (this.getGoodsZhengfu(piao[i].piao_form.invoice_table_data).zheng[0]) {
          data.push(this.getGoodsZhengfu(piao[i].piao_form.invoice_table_data).zheng[0])
        }
      }
      return data
    }
    this.data = this.getData()
    this.num = this.numJine(this.data)
    this.name = '销项正数金额'
    this.percentArr = this.getPercentArr(this.data)
  }

  export default {
    data () {
      return {
        iszhuan: true
      }
    },
    created () {
      this.getData()
    },
    methods: {
      getData () {
        //   this.AllData.prototype.b = 1
        ZhengfeiJine.prototype = new AllData(this)
        ZhengshuJine.prototype = new AllData(this)
        var data = new ZhengfeiJine()
        var data2 = new ZhengshuJine()
        console.log(data)
        console.log(data2)
      },
      change_title (a) {
        if (a === 'zhuan') {
          this.iszhuan = true
        }
        if (a === 'pu') {
          this.iszhuan = false
        }
      },
      click_exit () {
        this.$router.go(-1)
      }
    }
  }
</script>


